*{
  padding: 0;
  margin: 0;
  outline: none;
  box-sizing: border-box;
}
html,body,#root,.App{
  width: 100%;
  height: 100%;
  overflow-x:hidden;
}
.App{
  position: relative;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  background-color: white;
}
.nav-container{
  z-index: 5;
  width: 100%;
  height: 10%;
  padding: 1%;
  font-size: 1.5rem;
  border-bottom: 1px solid rgb(199, 199, 199);
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 10px 2px -20px black;
  .nav-logo{
    width: 20%;
    height: 100%;
    margin-right: 57.5%;
    display: flex;
    align-items: center;
    justify-content: left;
  }
  .nav-content{
    font-size: 1.25rem;
    font-weight: 100;
    width: 7.5%;
    height: 100%;
    display: flex;
    align-items:center;
    justify-content: center;
  }
  .nav-content:hover{
    transition: all 0.2s;
    background-color: #eee;
    border-radius: 5px;
    color: black;
  }
}
.main-container{
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  background-color: #eee;
}
.top-container{
  width: 100%;
  padding: 1%;
  display: flex;
}
.main-title-text{
  width: 50%;
  height: 500px;
  padding: 1%;
  background: linear-gradient(#eee,white);
  p{
    color: #0A1931;
    text-align: center;
    font-size: 2.25vh;
  }
  .title{
    font-size: 2.5rem;
    margin-bottom: 3vw;
  }
}
.main-title-text-top{
  width: 100%;
  height: 50%;
}
.main-title-text-bottom{
  width: 100%;
  height: 50%;
  display: flex;
}
.main-title-text-btn{
  width: 50%;
  height: 100%;
  padding: 5%;
}
.shadow{
  width: 100%;
    height: 100%;
    border-radius: 3vw;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
}
.shadow:hover{
  box-shadow:  5px 5px 5px 5px gray;
  color: #0A1931;
}
.main-title-img{
  width: 50%;
  height: 500px;
  padding:1%;
  background: linear-gradient(#eee,white);
  div{
    width: 100%;
    height: 100%;
    background: url(./cat.jpg);
    background-repeat: repeat;
    background-size: cover;
  }
}
.main-lost-box{
  width: 100%;
  height: auto;
  background-color: white;
}
.main-lost-box-watchword{
  width: 100%;
  height: 150px;
  display: flex;
  align-items: center;
  justify-content: center;
  .main-lost-box-watchword-box{
    width: 70%;
    height: 80%;
    border: 5px solid #FF8E00;
    background-color: #f9f1dc;
    color: #0A1931;
    border-radius:50px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .main-lost-box-watchword-one{
    width: 20%;
    display: flex;
    align-items: center;
    justify-content: center;
    color:#FF8E00 ;
  }
  .main-lost-box-watchword-two{
    width: 60%;
  }
  .main-lost-box-watchword-three{
    width: 20%;
    display: flex;
    align-items: center;
    justify-content: center;
    div{
      width: 50%;
      height: 6vh;
      border: 5px solid #8946A6;
      border-radius: 1vw;
      background-color: #8946A6;
      font-size: 2vh;
      font-weight: 700;
      color: white;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
  p{
    font-size: 2rem;
  }
}
.main-lost-cardbox{
  background-color: #eee;
  width: 100%;
  height: auto;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}
.cardbox-title{
  height:100%;
  padding: 2.5%;
  margin-top: 8vh;
  display: flex;
  flex-wrap: wrap;
  position: relative;
  p{
    font-size: 5vh;
    color: #0A1931;
  }
}
.card-box{
  position: relative;
  width: 300vw;
  height: auto;
  padding: 1%;
  display: flex;
  transform: translateX(0);
  transition: all 0.5s;
}
.card-btn1{
  z-index: 10;
  position: absolute;
  bottom:-60vh;
  left: 0vh;
  width: 5vh;
  height: 5vh;
  transform: translate(50%,-50%);
}
.card-btn2{
  z-index: 10;
  position: absolute;
  bottom:-60vh;
  right: 0vh;
  width: 5vh;
  height: 5vh;
  transform: translate(-50%,-50%);
}
.card{
  width: 22.5vw;
  margin-right:2.5vw;
  height: 400px;
  border-radius: 1vw;
  padding: 1vh;
  text-align: left;
  font-size: 1.25rem;
  background-color: FFFFFF;
  box-shadow: 2px 2px 1px 1px black;
  border: 0.5px solid black;
  img{
    width: 100%;
    height: 250px;
    border-bottom: #0096FF 10px solid;
  }
  .card-text{
    width: 100%;
  }
  p{
    margin: 8px;
  }
}
.search-main{
  width: 100%;
  height: 100vh;
  background: linear-gradient(#eee,white);;
  display: flex;
  align-items: center;
  justify-content: center;
}
.search-content-box{
  width: 80vw;
  height: 80vh;
  border-radius: 2vw;
  border: 2px solid gray;
  padding: 2%;
  display: flex;
  flex-wrap: wrap;
}
.search-bar{
  width: 100%;
  height: 200px;
  background-color: #eee;
  padding: 1vh;
}
.search-content1{
  width: 30%;
  height: 100%;
  flex-wrap: wrap;
  padding: 1%;
}
.btn{
  width: 100%;
  height: 5vh;
  border-radius: 2vw;
  background-color: white;
  color: #100720;
}
.btn:hover{
  background-color: #100720;
  color: white;
}
.search-content2{
  width: 70%;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  padding: 2.5%;
  border-radius: 2vw;
  border: 1px solid gray;
  img{
    width: 30%;
    height: 200px;
  }
  .basebox{
    width: 70%;
    height: 200px;
    padding-left: 2vw;
    p{
      font-size: 2vh;
      margin-bottom: 1vh;
    }
  }
}
.impobox{
  position: relative;
  padding-top: 2.5vw;
  width: 100%;
  height: calc( 100% - 200px);
  .title{
    margin-right: 1vw;
  }
  .btn-box{
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translate(-50%,-50%);
    display: flex;
    button{
      width: 2vw;
      height: 2vw;
      display: flex;
      align-items: center;
      justify-content:center;
      background-color: none;
      border: none;
    }
    div{
      width: 2vw;
      height: 2vw;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
}
.search-select{
  width: 100%;
  height: 5vh;
  border: 1;
  border-radius: 2vw;
  text-align: center;
}
.search-date-box{
  width: 100%;
  height: 5vh;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 1vw;
  p{
    width: 20%;
    font-size: 1.5vw;
    margin-right: 1vw;
    text-align: center;
  }
  select{
    width: 80%;
  }
}
.search-date{
  width: 80%;
  height: 5vh;
  border: 1;
  border-radius: 2vw;
  text-align: center;
}
.find-box{
  width: 100vw;
  height: 800px;
  display: flex;
  flex-wrap: wrap;
}
.find-box-title{
  width: 100vw;
  height: 200px;
}
.find-box-content{
  width: 100vw;
  height: 600px;
  position: relative;
}
.find-box-img{
  position: absolute;
  top:50%;
  left: 62.5%;
  transform: translate(-50%,-50%);
  width: 60%;
  height:90%;
  background: url(./dog.jpg);
  background-repeat: none;
  background-position: center;
  background-size: cover;
  border-radius: 3vw;
  border: 5px solid rgb(61, 178, 255);
}
.find-box-text{
  z-index: 8;
  position:absolute;
  top:50%;
  left: 22.5%;
  transform: translate(-50%,-50%);
  width: 35vw;
  height: 30vw;
  background-color: #FF8E00;
  border-radius: 3vw;
  font-size: 1vw;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  select{
    font-size: 1.75vw;
    width: 80%;
    height: 3vw;
    border-radius: 2vw;
    text-align: center;
    background-color: #100720;
    color: white;
  }
}